import React, { useEffect, useRef, useState } from "react";
import Map from "ol/Map";
import View from "ol/View";
import TileLayer from "ol/layer/Tile";
import OSM from "ol/source/OSM";
import { fromLonLat, toLonLat } from "ol/proj";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import GeoJSON from "ol/format/GeoJSON";
import { Style, Stroke, Circle as CircleStyle, Fill, Text } from "ol/style";
import Feature from "ol/Feature";
import Point from "ol/geom/Point";
import Overlay from "ol/Overlay";
import { toStringHDMS } from "ol/coordinate";

const backendUrl = "/api/route"; 
const scoreUrl = "http://127.0.0.1:5000/api/score"; 
// 新图层URL
const cctvUrl = "http://127.0.0.1:5000/api/layer/cctv";
const popdenUrl = "http://127.0.0.1:5000/api/layer/popden";

const RoadView = () => {
  const mapRef = useRef();
  const popupRef = useRef();
  const [map, setMap] = useState(null);
  const [points, setPoints] = useState([]); // 存储起点终点
  const [routePlanningMode, setRoutePlanningMode] = useState(false); // 路径规划模式
  const [popupInfo, setPopupInfo] = useState(null); // 弹出信息
  
  // 使用useRef保存所有图层引用
  const layersRef = useRef({
    route: null,
    roadSafety: null,
    cctv: null,
    popden: null
  });
  
  // 图层可见性状态
  const [layerVisibility, setLayerVisibility] = useState({
    roadSafety: true,
    cctv: true,
    popden: true
  });
  
  const popupOverlayRef = useRef();
  const clickHandlerRef = useRef(); // 用于保存和管理点击处理函数
  
  // 为CCTV创建样式
  const createCctvStyle = () => {
    return new Style({
      image: new CircleStyle({
        radius: 6,
        fill: new Fill({
          color: 'rgba(0, 0, 255, 0.7)' // 蓝色填充
        }),
        stroke: new Stroke({
          color: '#ffffff',
          width: 2
        })
      })
    });
  };
  
  // 为人口密度创建样式
  const createPopdenStyle = (feature) => {
    // 获取人口密度值（具体属性名可能需要根据数据结构调整）
    const density = feature.get('pop_density') || 
                   feature.get('population_density') || 
                   feature.get('DENSITY') || 
                   feature.get('density') || 0;
    
    // 根据密度值选择颜色
    let color;
    if (density > 10000) color = 'rgba(204, 0, 0, 0.5)';      // 很高 - 红色
    else if (density > 5000) color = 'rgba(255, 51, 0, 0.5)'; // 高 - 橙红色
    else if (density > 2000) color = 'rgba(255, 153, 0, 0.5)';// 中高 - 橙色
    else if (density > 1000) color = 'rgba(255, 255, 0, 0.5)';// 中 - 黄色
    else if (density > 500) color = 'rgba(204, 255, 51, 0.5)';// 中低 - 黄绿色
    else color = 'rgba(153, 255, 102, 0.5)';                  // 低 - 绿色
    
    return new Style({
      fill: new Fill({
        color: color
      }),
      stroke: new Stroke({
        color: '#000000',
        width: 1
      })
    });
  };
  
  // 初始化地图
  useEffect(() => {
    console.log("初始化地图...");
    
    // 初始化路径图层
    const routeLayer = new VectorLayer({
      source: new VectorSource(),
      style: function(feature) {
        // 根据feature属性动态设置样式
        let color = "red"; // 默认颜色
        
        // 尝试从多个可能的位置获取颜色
        const properties = feature.getProperties();
        if (properties.color) {
          color = properties.color;
        } else if (properties.properties && properties.properties.color) {
          color = properties.properties.color;
        }
        
        // 判断是否为起点或终点
        const name = feature.get("name");
        if (name === "Start Point" || name === "End Point") {
          return new Style({
            image: new CircleStyle({
              radius: 8,
              fill: new Fill({ 
                color: name === "Start Point" ? "green" : "red" 
              }),
              stroke: new Stroke({ color: "#fff", width: 2 })
            }),
            text: new Text({
              text: name,
              offsetY: -15,
              fill: new Fill({ color: "#000" }),
              stroke: new Stroke({ color: "#fff", width: 3 }),
              font: '14px Arial'
            })
          });
        } else {
          // 路径样式
          return new Style({
            stroke: new Stroke({
              color: color,
              width: 6,
              lineCap: 'round',
              lineJoin: 'round'
            })
          });
        }
      },
      zIndex: 10 // 确保路径在最上层
    });
    
    // 创建CCTV图层
    const cctvLayer = new VectorLayer({
      source: new VectorSource(),
      style: createCctvStyle,
      zIndex: 8
    });
    
    // 创建人口密度图层
    const popdenLayer = new VectorLayer({
      source: new VectorSource(),
      style: createPopdenStyle,
      zIndex: 3
    });
    
    // 道路安全图层
    // const roadSafetyLayer = new ImageLayer({
    //   source: new ImageWMS({
    //     url: serverUrl,
    //     params: {
    //       LAYERS: "hk_road_scores:scored_roads_feature",
    //       TILED: true,
    //     },
    //     ratio: 1,
    //     serverType: "geoserver",
    //   }),
    //   zIndex: 5
    // });

    // 创建地图
    const mapObj = new Map({
      target: mapRef.current,
      layers: [
        new TileLayer({ source: new OSM(), zIndex: 0 }),
        popdenLayer, // 底层
        // roadSafetyLayer, // 中间层
        cctvLayer, // 高层
        routeLayer // 最上层
      ],
      view: new View({
        center: fromLonLat([114.1384, 22.2834]),
        zoom: 16,
      }),
    });

    // 保存图层引用
    layersRef.current = {
      route: routeLayer,
      roadSafety: null,
      cctv: cctvLayer,
      popden: popdenLayer
    };

    // 创建弹出覆盖层
    const popupOverlay = new Overlay({
      element: popupRef.current,
      autoPan: true,
      autoPanAnimation: {
        duration: 250,
      },
    });
    mapObj.addOverlay(popupOverlay);
    popupOverlayRef.current = popupOverlay;

    setMap(mapObj);
    
    // 加载图层数据
    loadLayerData();

    return () => mapObj.setTarget(undefined);
  }, []);
  
  // 加载图层数据
  const loadLayerData = () => {
    console.log("加载图层数据...");
    
    // 加载CCTV数据
    fetch(process.env.PUBLIC_URL + "/cctv.geojson")
      .then(res => res.json())
      .then(geojson => {
        const features = new GeoJSON({
          dataProjection: 'EPSG:4326',
          featureProjection: 'EPSG:3857'
        }).readFeatures(geojson);
        layersRef.current.cctv.getSource().addFeatures(features);
      });
    
    // 加载人口密度数据
    fetch(process.env.PUBLIC_URL + "/popden.geojson")
      .then(res => res.json())
      .then(geojson => {
        const features = new GeoJSON({
          dataProjection: 'EPSG:4326',
          featureProjection: 'EPSG:3857'
        }).readFeatures(geojson);
        layersRef.current.popden.getSource().addFeatures(features);
      });
  };

  // 关键修复：将地图点击事件处理逻辑独立出来，并在模式切换时重新绑定
  useEffect(() => {
    if (!map) return;
    
    console.log("重新绑定点击事件处理器，当前模式:", routePlanningMode ? "路径规划" : "查询");
    
    // 删除任何现有的点击事件监听器
    if (clickHandlerRef.current) {
      map.un("singleclick", clickHandlerRef.current);
      console.log("已删除旧的点击事件处理器");
    }
    
    // 创建新的点击事件处理函数
    const handleMapClick = function(evt) {
      const coordinate = evt.coordinate;
      const lonlat = toLonLat(coordinate);
      
      console.log("地图点击，当前模式:", routePlanningMode ? "路径规划模式" : "查询模式");
      
      if (routePlanningMode) {
        // 路径规划模式：选择起点和终点
        console.log("处理路径规划点击，当前点数:", points.length);
        
        setPoints((prev) => {
          if (prev.length < 2) {
            console.log("添加新点:", lonlat);
            return [...prev, lonlat];
          } else {
            // 如果已经有两个点，重置为新起点
            console.log("重置路径，新起点:", lonlat);
            layersRef.current.route.getSource().clear();
            return [lonlat];
          }
        });
      } else {
        // 非规划模式：首先检查是否点击了特殊图层，否则显示路段得分
        const features = map.getFeaturesAtPixel(evt.pixel);
        if (features && features.length > 0) {
          // 检查是否点击了CCTV或人口密度图层的要素
          const cctvFeature = features.find(f => 
            layersRef.current.cctv.getSource().hasFeature(f));
          
          const popdenFeature = features.find(f => 
            layersRef.current.popden.getSource().hasFeature(f));
          
          if (cctvFeature) {
            console.log("点击了CCTV");
            const props = cctvFeature.getProperties();
            showSpecialLayerPopup(coordinate, "CCTV摄像头", props);
            return;
          }
          
          if (popdenFeature) {
            console.log("点击了人口密度区域");
            const props = popdenFeature.getProperties();
            showSpecialLayerPopup(coordinate, "人口密度信息", props);
            return;
          }
        }
        
        // 没有点击特殊图层，显示路段得分
        console.log("处理查询点击，获取路段得分");
        fetchRoadScore(lonlat, coordinate);
      }
    };
    
    // 保存引用以便稍后可以移除
    clickHandlerRef.current = handleMapClick;
    
    // 添加点击事件监听器
    map.on("singleclick", handleMapClick);
    console.log("已添加新的点击事件处理器");
    
    return () => {
      if (map && clickHandlerRef.current) {
        map.un("singleclick", clickHandlerRef.current);
        console.log("清理时移除点击事件处理器");
      }
    };
  }, [map, routePlanningMode, points.length]); // 关键：当routePlanningMode或points改变时重新绑定

  // 监听图层可见性变化
  useEffect(() => {
    if (!map) return;
    
    // 更新图层可见性
    Object.entries(layerVisibility).forEach(([key, isVisible]) => {
      const layer = layersRef.current[key];
      if (layer) {
        layer.setVisible(isVisible);
        console.log(`图层 ${key} 可见性设置为:`, isVisible);
      }
    });
  }, [layerVisibility, map]);

  // 获取路段得分
  const fetchRoadScore = (lonlat, coordinate) => {
    fetch(scoreUrl, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ point: lonlat }),
    })
      .then((res) => res.json())
      .then((data) => {
        // 显示得分信息
        showRoadScorePopup(coordinate, data);
      })
      .catch(err => {
        console.error("获取得分失败:", err);
        // 模拟得分数据（实际应该从后端获取）
        const mockData = {
          score: (Math.random() * 0.8 + 0.2).toFixed(2),
          grade: ["A", "B", "C", "D", "E"][Math.floor(Math.random() * 5)],
          road_name: "示例路段" + Math.floor(Math.random() * 100)
        };
        showRoadScorePopup(coordinate, mockData);
      });
  };

  // 显示道路得分弹出信息
  const showRoadScorePopup = (coordinate, data) => {
    if (popupOverlayRef.current && data) {
      setPopupInfo({
        type: 'roadScore',
        ...data
      });
      popupOverlayRef.current.setPosition(coordinate);
    }
  };
  
  // 显示特殊图层弹出信息
  const showSpecialLayerPopup = (coordinate, title, properties) => {
    // 过滤掉几何和内部属性
    const filteredProps = Object.entries(properties).filter(([key, value]) => 
      key !== 'geometry' && !key.startsWith('_') && typeof value !== 'object'
    );
    
    setPopupInfo({
      type: 'specialLayer',
      title: title,
      properties: filteredProps
    });
    
    popupOverlayRef.current.setPosition(coordinate);
  };

  // 关闭弹出窗口
  const closePopup = () => {
    popupOverlayRef.current.setPosition(undefined);
    setPopupInfo(null);
  };

  // 切换图层可见性
  const toggleLayerVisibility = (layerName) => {
    console.log(`切换图层 ${layerName} 可见性`);
    setLayerVisibility(prev => ({
      ...prev,
      [layerName]: !prev[layerName]
    }));
  };

  // 切换路径规划模式
  const toggleRoutePlanningMode = () => {
    console.log("模式切换:", !routePlanningMode ? "路径规划模式" : "查询模式");
    
    setRoutePlanningMode(!routePlanningMode);
    
    // 清除之前的点和路径
    setPoints([]);
    if (layersRef.current.route) {
      layersRef.current.route.getSource().clear();
    }
    
    // 关闭任何打开的弹窗
    if (popupOverlayRef.current) {
      popupOverlayRef.current.setPosition(undefined);
      setPopupInfo(null);
    }
  };

  // 监听points变化，2点后请求后端
  useEffect(() => {
    console.log("points更新:", points.length, "当前模式:", routePlanningMode ? "路径规划" : "查询");
    
    if (points.length === 2 && routePlanningMode) {
      console.log("准备请求路径:", points[0], "->", points[1]);
      
      fetch(backendUrl, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ start: points[0], end: points[1] }),
      })
        .then((res) => res.json())
        .then((geojson) => {
          // 清除之前的路径
          layersRef.current.route.getSource().clear();
          
          console.log("收到GeoJSON数据:", geojson);
          console.log("特征数量:", geojson.features ? geojson.features.length : 0);
          
          // 检查是否是FeatureCollection格式
          if (geojson.type === "FeatureCollection" && Array.isArray(geojson.features)) {
            // 确保每个特征都有properties
            geojson.features.forEach((feature, index) => {
              if (!feature.properties) {
                feature.properties = {};
              }
              
              // 如果颜色丢失，手动设置默认颜色
              if (!feature.properties.color) {
                const defaultColors = ["#FF4B4B", "#FFD700", "#4BFF4B"];
                feature.properties.color = defaultColors[index % defaultColors.length];
              }
              
              // 如果描述丢失，设置默认描述
              if (!feature.properties.desc) {
                const defaultDescs = ["Shortest Path", "Safest Path", "Balanced Path"];
                feature.properties.desc = defaultDescs[index % defaultDescs.length];
              }
            });
            
            // 一次性读取所有特征
            const features = new GeoJSON({
              // 明确指定坐标系
              dataProjection: 'EPSG:4326',  // GeoJSON使用的坐标系
              featureProjection: 'EPSG:3857' // 地图使用的坐标系
            }).readFeatures(geojson);
            
            console.log("解析后的特征数量:", features.length);
            
            // 添加所有路径到图层
            layersRef.current.route.getSource().addFeatures(features);
          } else {
            console.error("Received invalid FeatureCollection format:", geojson);
          }
          
          // 起点
          const startFeature = new Feature({
            geometry: new Point(fromLonLat(points[0])),
            name: "Start Point"
          });
          
          // 终点
          const endFeature = new Feature({
            geometry: new Point(fromLonLat(points[1])),
            name: "End Point"
          });
          
          // 添加起点和终点到图层
          layersRef.current.route.getSource().addFeatures([startFeature, endFeature]);
          
          console.log("Path and marker points added");
        })
        .catch(err => {
          console.error("Failed to calculate path:", err);
          alert("Failed to calculate path, please try again");
        });
    }
  }, [points, routePlanningMode]);

  // 加载路网
  useEffect(() => {
    if (!map) return;
    fetch(process.env.PUBLIC_URL + "/roads.geojson")
      .then(res => res.json())
      .then(geojson => {
        const roadSource = new VectorSource({
          features: new GeoJSON().readFeatures(geojson, {
            dataProjection: "EPSG:4326",
            featureProjection: "EPSG:3857"
          })
        });
        const roadLayer = new VectorLayer({
          source: roadSource,
          style: feature => {
            const score = feature.get('total_safety_score') || 0;
            let color = "#888";
            if (score > 0.8) color = "#4CAF50";
            else if (score > 0.6) color = "#8BC34A";
            else if (score > 0.4) color = "#FFC107";
            else if (score > 0.2) color = "#FF9800";
            else color = "#F44336";
            return new Style({
              stroke: new Stroke({
                color,
                width: 2
              })
            });
          },
          zIndex: 2
        });
        map.addLayer(roadLayer);
      });
  }, [map]);

  // 加载CCTV
  useEffect(() => {
    if (!map) return;
    fetch(process.env.PUBLIC_URL + "/cctv.geojson")
      .then(res => res.json())
      .then(geojson => {
        const features = new GeoJSON({
          dataProjection: 'EPSG:4326',
          featureProjection: 'EPSG:3857'
        }).readFeatures(geojson);
        layersRef.current.cctv.getSource().addFeatures(features);
      });
  }, [map]);

  // 加载人口密度
  useEffect(() => {
    if (!map) return;
    fetch(process.env.PUBLIC_URL + "/popden.geojson")
      .then(res => res.json())
      .then(geojson => {
        const features = new GeoJSON({
          dataProjection: 'EPSG:4326',
          featureProjection: 'EPSG:3857'
        }).readFeatures(geojson);
        layersRef.current.popden.getSource().addFeatures(features);
      });
  }, [map]);

  return (
    <div>
      <div ref={mapRef} style={{ width: "100%", height: "100vh" }} />
      
      {/* 控制面板 */}
      <div style={{ 
        position: "absolute", 
        top: 10, 
        left: 10, 
        background: "white", 
        padding: 15,
        borderRadius: 8,
        boxShadow: "0 2px 10px rgba(0,0,0,0.2)",
        maxWidth: 300
      }}>
        <h3 style={{ margin: "0 0 15px 0" }}>Safenight-HKU Road Safety Map</h3>
        
        {/* 模式切换按钮 */}
        <button 
          onClick={toggleRoutePlanningMode}
          style={{
            padding: "8px 15px",
            backgroundColor: routePlanningMode ? "#4CAF50" : "#2196F3",
            color: "white",
            border: "none",
            borderRadius: 4,
            cursor: "pointer",
            width: "100%",
            marginBottom: 15,
            fontWeight: "bold"
          }}
        >
          {routePlanningMode ? "Exit Path Planning Mode" : "Enter Path Planning Mode"}
        </button>
        
        {/* 模式说明 */}
        <div style={{ 
          padding: 10, 
          backgroundColor: routePlanningMode ? "#e8f5e9" : "#e3f2fd",
          borderRadius: 4,
          marginBottom: 15
        }}>
          {routePlanningMode ? (
            <div>
              <div style={{ fontWeight: "bold", marginBottom: 5 }}>Path Planning Mode</div>
              <div>Please click on the start and end points on the map</div>
              <div>Current number of selected points: {points.length}</div>
              {points.length === 2 && <div>Path recommended, click on the map to reselect the start point</div>}
            </div>
          ) : (
            <div>
              <div style={{ fontWeight: "bold", marginBottom: 5 }}>Query Mode</div>
              <div>Click on the elements on the map to view detailed information</div>
            </div>
          )}
        </div>
        
        {/* 图层控制 */}
        <div style={{ marginTop: 15, marginBottom: 15 }}>
          <div style={{ fontWeight: "bold", marginBottom: 8 }}>Map Layers</div>
          
          <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
            <input 
              type="checkbox" 
              id="cctvLayer"
              checked={layerVisibility.cctv}
              onChange={() => toggleLayerVisibility('cctv')}
              style={{ marginRight: 5 }}
            />
            <label htmlFor="cctvLayer">CCTV</label>
          </div>
          
          <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
            <input 
              type="checkbox" 
              id="popdenLayer"
              checked={layerVisibility.popden}
              onChange={() => toggleLayerVisibility('popden')}
              style={{ marginRight: 5 }}
            />
            <label htmlFor="popdenLayer">Population Density</label>
          </div>
        </div>
        
        {/* 图例 */}
        <div style={{ marginTop: 15 }}>
          <div style={{ fontWeight: "bold", marginBottom: 8 }}>Legend</div>
          
          {/* 路径图例 */}
          <div style={{ marginBottom: 15 }}>
            <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
              <div style={{ 
                width: 15, 
                height: 15, 
                borderRadius: "50%", 
                backgroundColor: "green",
                marginRight: 8
              }}></div>
              <span>Start Point</span>
            </div>
            <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
              <div style={{ 
                width: 15, 
                height: 15, 
                borderRadius: "50%", 
                backgroundColor: "red",
                marginRight: 8
              }}></div>
              <span>End Point</span>
            </div>
            <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
              <div style={{ 
                width: 20, 
                height: 6, 
                backgroundColor: "#FF4B4B",
                marginRight: 8
              }}></div>
              <span>Shortest Path</span>
            </div>
            <div style={{ display: "flex", alignItems: "center", marginBottom: 5 }}>
              <div style={{ 
                width: 20, 
                height: 6, 
                backgroundColor: "#FFD700",
                marginRight: 8
              }}></div>
              <span>Safest Path</span>
            </div>
            <div style={{ display: "flex", alignItems: "center" }}>
              <div style={{ 
                width: 20, 
                height: 6, 
                backgroundColor: "#4BFF4B",
                marginRight: 8
              }}></div>
              <span>Balanced Path</span>
            </div>
          </div>
          
          {/* 图层图例 */}
          <div>
            <div style={{ marginBottom: 5 }}>
              <div style={{ display: "flex", alignItems: "center" }}>
                <div style={{ 
                  width: 15, 
                  height: 15, 
                  borderRadius: "50%", 
                  backgroundColor: "rgba(0, 0, 255, 0.7)",
                  border: "2px solid white",
                  marginRight: 8
                }}></div>
                <span>CCTV Cameras</span>
              </div>
            </div>
            
            <div style={{ marginBottom: 5 }}>
              <div style={{ fontWeight: "bold", marginBottom: 3, fontSize: 13 }}>Population Density（per/km²）</div>
              <div style={{ display: "flex", alignItems: "center", marginLeft: 5, marginBottom: 2 }}>
                <div style={{ 
                  width: 20, 
                  height: 15, 
                  backgroundColor: "rgba(204, 0, 0, 0.5)",
                  marginRight: 8
                }}></div>
                <span>10000+</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", marginLeft: 5, marginBottom: 2 }}>
                <div style={{ 
                  width: 20, 
                  height: 15, 
                  backgroundColor: "rgba(255, 153, 0, 0.5)",
                  marginRight: 8
                }}></div>
                <span>2000-5000</span>
              </div>
              <div style={{ display: "flex", alignItems: "center", marginLeft: 5, marginBottom: 2 }}>
                <div style={{ 
                  width: 20, 
                  height: 15, 
                  backgroundColor: "rgba(153, 255, 102, 0.5)",
                  marginRight: 8
                }}></div>
                <span>&lt;500</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      {/* 弹出信息窗口 */}
      <div 
        ref={popupRef} 
        style={{ 
          position: "absolute", 
          backgroundColor: "white", 
          boxShadow: "0 1px 4px rgba(0,0,0,0.2)",
          padding: 15,
          borderRadius: 10,
          border: "1px solid #cccccc",
          minWidth: 180,
          maxWidth: 300,
          maxHeight: "60vh",
          overflowY: "auto",
          display: popupInfo ? "block" : "none"
        }}
      >
        <button 
          onClick={closePopup}
          style={{
            position: "absolute",
            top: 2,
            right: 8,
            border: "none",
            background: "none",
            fontSize: 16,
            cursor: "pointer"
          }}
        >✕</button>
        
        {/* 道路得分信息 */}
        {popupInfo && popupInfo.type === 'roadScore' && (
          <div>
            <h4 style={{ margin: "0 0 10px 0" }}>{popupInfo.road_name || "Road Information"}</h4>
            <div style={{ marginBottom: 5 }}>
              <span style={{ fontWeight: "bold" }}>Safety Score: </span>
              <span>{popupInfo.score}</span>
            </div>
            <div style={{ marginBottom: 5 }}>
              <span style={{ fontWeight: "bold" }}>Safety Level: </span>
              <span style={{ 
                fontWeight: "bold",
                color: 
                  popupInfo.grade === "A" ? "#4CAF50" :
                  popupInfo.grade === "B" ? "#8BC34A" :
                  popupInfo.grade === "C" ? "#FFC107" :
                  popupInfo.grade === "D" ? "#FF9800" : "#F44336"
              }}>
                {popupInfo.grade}
              </span>
            </div>
          </div>
        )}
        
        {/* 特殊图层信息 */}
        {popupInfo && popupInfo.type === 'specialLayer' && (
          <div>
            <h4 style={{ margin: "0 0 10px 0" }}>{popupInfo.title}</h4>
            {popupInfo.properties.map(([key, value], index) => (
              <div key={index} style={{ marginBottom: 5 }}>
                <span style={{ fontWeight: "bold" }}>{key}: </span>
                <span>{value}</span>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default RoadView;